import Vue from 'vue'
import VueRouter from 'vue-router'
import index from '../views/Home/index.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: index,
    meta: {
      requireAuth: true
    }
  },
  {
    path: "/login", //  登录页面
    name: "Login",
    component: () =>
      import(/* webpackChunkName: "login" */ "../views/user/Login.vue"),
  },
  {
    path: "/register", //  注册页面
    name: "Register",
    component: () =>
      import(/* webpackChunkName: "register" */ "../views/user/Register.vue"),
  },
  {
    path: "/resetPwd", //  注册页面
    name: "ResetPwd",
    component: () =>
      import(/* webpackChunkName: "register" */ "../views/user/ResetPwd.vue"),
  },
  {
    path: "*", //  404页面
    name: "Error_404",
    component: () =>
      import(/* webpackChunkName: "error_404" */ "../views/errorPage/404.vue"),
  },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
})

export default router
