<template>
  <el-table
    class="file-table"
    :data="tableData"
    height="calc(100vh - 202px)"
    style="width: 100%"
    v-loading="loading"
    @selection-change="handleSelectRow">
    <!-- 勾选框 -->
    <el-table-column
      type="selection"
      width="56"
      align="center" key="1"></el-table-column>
    <el-table-column label prop="isDir" width="60" align="center">
      <template slot-scope="scope">
        <img :src="setFileImg(scope.row)" style="width: 30px" />
      </template>
    </el-table-column>
    <el-table-column prop="fileName" label="文件名" key="2">
      <template slot-scope="scope">
        <div style="cursor: pointer" @click="handleFileNameClick(scope.row)">
          {{
            scope.row.extendName
              ? `${scope.row.fileName}.${scope.row.extendName}`
              : `${scope.row.fileName}`
          }}
        </div>
      </template>
    </el-table-column>
    <el-table-column
      label="所在路径"
      prop="filePath"
      show-overflow-tooltip
      v-if="fileType != 0" key="3">
      <template slot-scope="scope"  >
        <div style="cursor: pointer"
          title="点击跳转"
          v-if="fileType!=6"
          @click="$router.push({query: { fileType: 0, filePath: scope.row.filePath }})">{{ scope.row.filePath }}
        </div>
        <div style="cursor: pointer"
          v-if="fileType == 6">{{ scope.row.filePath }}
        </div>
      </template>
    </el-table-column>
    <!-- 通过 v-if 来控制 类型 列是否显示 -->
    <el-table-column
      prop="extendName"
      label="类型"
      width="100"
      v-if="selectedColumnList.includes('extendName')" key="4">
      <template slot-scope="scope">
          <span>{{ scope.row.extendName ? scope.row.extendName : '文件夹' }}</span>
      </template>
    </el-table-column>
    <!-- 通过 v-if 来控制 大小 列是否显示 -->
    <el-table-column
      prop="fileSize"
      label="大小"
      width="100"
      v-if="selectedColumnList.includes('fileSize')" key="5">
        <template slot-scope="scope">
              <span>{{ calculateFileSize(scope.row.fileSize) }}</span>
        </template>
    </el-table-column>
    <!-- 通过 v-if 来控制 修改日期 列是否显示 -->
    <el-table-column
      prop="uploadTime"
      label="修改日期"
      width="180"
      v-if="selectedColumnList.includes('uploadTime')" key="6">
    </el-table-column>
    
     <!-- 表格操作列 自定义表格头，原有的 label="操作" 需要删除，宽度动态变化 -->
     <el-table-column :width="operaColumnIsFold ? 300 : 100" v-if="fileType != 6" key="7">
      <!-- 自定义表格头 -->
      <template slot="header" >
        <span>操作</span>
        <i class="el-icon-circle-plus"
          title="展开"
          @click="operaColumnIsFold = true"></i>
        <i class="el-icon-remove"
          title="折叠"
          @click="operaColumnIsFold = false"></i>
      </template>
      <template slot-scope="scope">
       <!-- 操作列展开状态下的按钮 通过v-if控制 -->
       <div class="opera-unfold" v-if="operaColumnIsFold">
          <el-button
            type="text"
            size="small"
            @click.native="handleClickDelete(scope.row)"
            >删除</el-button>
          <el-button
            type="text"
            size="small"
            @click.native="handleClickMove(scope.row)"
            >移动</el-button>
            <el-button
            type="text"
            size="small"
            @click.native="handleClickCopy(scope.row)"
            >复制到</el-button>
          <el-button
            type="text"
            size="small"
            @click.native="handleClickRename(scope.row)"
            >重命名</el-button>
          <el-button type="text" size="small" v-if="scope.row.isDir === 0">
            <a :href="`/api/filetransfer/downloadfile?userFileId=${scope.row.userFileId}`"
              target="_blank"
              style="display: block; color: inherit">下载</a>
        </el-button>
        </div>
         <!-- 操作列收缩状态下的按钮 通过v-else控制 -->
        <el-dropdown trigger="click" v-else>
          <el-button size="mini">
            操作
            <i class="el-icon-arrow-down el-icon--right"></i>
          </el-button>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item @click.native="handleClickDelete(scope.row)"
              >删除</el-dropdown-item
            >
            <el-dropdown-item @click.native="handleClickMove(scope.row)"
              >移动</el-dropdown-item
            >
            <el-dropdown-item @click.native="handleClickCopy(scope.row)"
              >复制到</el-dropdown-item
            >
            <el-dropdown-item @click.native="handleClickRename(scope.row)"
              >重命名</el-dropdown-item
            >
            <el-dropdown-item v-if="scope.row.isDir === 0">
                <a :href="`/api/filetransfer/downloadfile?userFileId=${scope.row.userFileId}`"
                  target="_blank"
                  style="display: block; color: inherit">下载</a>
            </el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </template>
    </el-table-column>
    
  </el-table>
</template>

<script>
import { deleteFile } from '@/request/file.js' //  引入接口
import { renameFile } from '@/request/file.js' //  引入文件重命名接口

export default {
  name: "FileTable",
  props: {
    // 表格数据，同时需要删除原本在 data( return { } ) 中的 tableData，否则会报错
    tableData: {
      type: Array,
      required: true
    },
    // 表格加载状态
    loading: {
      type: Boolean,
      required: true
    },
    // 文件类型
    fileType: {
        type: Number,
        required: true
    },
  },
  data() {
    return {
      operaColumnIsFold: sessionStorage.getItem('operaColumnIsFold') || false ,//  表格操作列-是否收缩
      //  文件图片Map映射
      fileImgMap: {
        apk: require('@/assets/image/apk.png'),
        csv: require('@/assets/image/csv.png'),
        doc: require('@/assets/image/doc.png'),
        excel: require('@/assets/image/excel.png'),
        exe: require('@/assets/image/exe.png'),
        fold: require('@/assets/image/fold.png'),
        gif: require('@/assets/image/gif.png'),
        html: require('@/assets/image/html.png'),
        json: require('@/assets/image/json.png'),
        mp3: require('@/assets/image/mp3.png'),
        mp4: require('@/assets/image/mp4.png'),
        other: require('@/assets/image/other.png'),
        pdf: require('@/assets/image/pdf.png'),
        ppt: require('@/assets/image/ppt.png'),
        rar: require('@/assets/image/rar.png'),
        svg: require('@/assets/image/svg.png'),
        txt: require('@/assets/image/txt.png'),
        zip: require('@/assets/image/zip.png')
      }
    }
  },
  computed: {
    // 表格显示列
    selectedColumnList() {
      return this.$store.getters.selectedColumnList;
    }
  },
  watch: {
    // 监听收缩状态变化，存储在sessionStorage中，保证页面刷新时仍然保存设置的状态
    operaColumnIsFold(newValue) {
     
      sessionStorage.setItem('operaColumnIsFold', newValue);
    }
  },
  created() {
    
    this.operaColumnIsFold = sessionStorage.getItem('operaColumnIsFold') === 'true'; //  读取保存的状态
  },
  methods: {
    //  根据文件扩展名设置文件图片
    setFileImg(row) {
      if (!row.extendName) {
        //  文件夹
        return this.fileImgMap.fold
      } else if (['jpg', 'png', 'jpeg'].includes(row.extendName)) {
        // 图片类型，直接显示缩略图
        return this.downloadImgMin(row)
      } else {
        const fileTypeMap = {
          pptx: 'ppt',
          doc: 'word',
          docx: 'doc',
          xls: 'excel',
          xlsx: 'excel'
        }
        //  可以识别文件类型的文件
        return this.fileImgMap[row.extendName] || this.fileImgMap[fileTypeMap[row.extendName]] || this.fileImgMap.other}
    },
    //  计算文件大小
    calculateFileSize(size) {
        const B = 1024
        const KB = Math.pow(1024, 2)
        const MB = Math.pow(1024, 3)
        const GB = Math.pow(1024, 4)
        if (!size) {
            return '_'
        } else if (size < KB) {
            return (size / B).toFixed(0) + 'KB'
        } else if (size < MB) {
            return (size / KB).toFixed(1) + 'MB'
        } else if (size < GB) {
            return (size / MB).toFixed(2) + 'GB'
        } else {
            return (size / GB).toFixed(3) + 'TB'
        }
    },
          // 文件名点击事件
  handleFileNameClick(row) {
      const Video=['mp4','mp3'];

      //  若是目录则进入目录
      if (row.isDir) {
          this.$router.push({
              query: {
                  filePath: `${row.filePath}${row.fileName}/`,
                  fileType: 0
              }
          })
      }
      if(Video.includes(row.extendName)&&this.fileType!=6){
        let videoUrl = row.fileUrl;
        let realVideoUrl=videoUrl.replace("http://39.101.74.48:9000/xpan","");
        let url = "api"+realVideoUrl;
        window.open(url);
      }
      
      else {
          //  若当前点击项是图片
          const PIC = ['png', 'jpg', 'jpeg', 'gif', 'svg']
          if (PIC.includes(row.extendName)) {
            let url=row.fileUrl;
            let realUrl=url.replace("http://39.101.74.48:9000/xpan","");
              let data = {
                  imgReviewVisible: true,
                  imgReviewList: [{
                      fileUrl: `/api${realUrl}`,
                      downloadLink: `/api/filetransfer/downloadfile?userFileId=${row.userFileId}`,
                      fileName: row.fileName,
                      extendName: row.extendName
                  }],
                  activeIndex: 0
              }
              this.$store.commit('setImgReviewData', data)    //    触发图片在线查看
          }
      }
  },
   // 删除按钮 - 点击事件
   handleClickDelete(row) {
      // 消息弹框提示用户
      this.$confirm('此操作将删除该文件，可在回收站恢复, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          // 确定按钮 点击事件 调用删除文件接口
          deleteFile(row).then((res) => {
            if (res.success) {
              this.$emit('getTableData') //  刷新文件列表
              this.$message.success('删除成功')
            } else {
              this.$message.error(res.message)
            }
          })
        })
        .catch(() => {
          //  取消
          this.$message({
            type: 'info',
            message: '已取消删除'
          })
        })
    },
   // 移动按钮 - 点击事件
    handleClickMove(row) {
        this.$emit('handleSelectFile', false, row) // true/false 操作类型：批量移动/单文件操作；row 当前行文件数据
        this.$emit('handleMoveFile', true) // true/false 打开/关闭移动文件对话框
    },

     // 文件复制按钮 - 点击事件
     handleClickCopy(row) {
      //console.log("复制", row.fileName);
        this.$emit('handleSelectFile', false, row) // true/false 操作类型：批量移动/单文件操作；row 当前行文件数据
        this.$emit('handleCopyFile', true) // true/false 打开/关闭移动文件对话框
    },


    // 重命名按钮 - 点击事件
  handleClickRename(row) {
      var fileName = row.fileName
      this.$prompt('请输入文件名', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        inputValue: fileName,
        inputPattern:/^[\u4e00-\u9fa5a-zA-Z-z0-9]+$/, //  文件名不能为空,只能是数字，字母，汉字
        inputErrorMessage: '请输入正确的文件名！',
        closeOnClickModal: false
      })
        .then(({ value }) => {
          // 确定按钮 调用重命名接口
          renameFile({
            ...row,
            fileName: value
          }).then((res) => {
            if (res.success) {
              this.$emit('getTableData') //  刷新文件列表
              this.$message.success('文件已重命名')
            } else {
              this.$message.error(res.message)
            }
          })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '取消'
          })
        })
    },
    // 表格行勾选事件
    handleSelectRow(selection) {
      this.$emit('handleSelectFile', true, selection) // true/false 批量移动/单文件操作；selection 勾选的表格行数据
    },


  },
};
</script>

<style lang="stylus" scoped>
// 表格操作列-表头图标样式调整
.file-table {
  // 调整滚动条样式
  >>> .el-table__body-wrapper {
    setScrollbar(8px, #EBEEF5, #909399);
  }
}
.el-icon-circle-plus, .el-icon-remove {
  margin-left: 8px;
  cursor: pointer;
  font-size: 16px;

  &:hover {
    opacity: 0.5;
  }
}
</style>