<template>
  <div class="copy-dialog-wrapper">
    <!-- 移动文件-选择目录模态框 -->
    <el-dialog title="选择目录" :visible.sync="dialogCopyFile.visible">
      <div class="el-dialog-div">
        <el-tree
          :data="dialogCopyFile.fileTree"
          :props="defaultProps"
          :highlight-current="true"
          default-expand-all
          @node-click="selectNodeClick"
        ></el-tree>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="$emit('handleCopyFile', false)">取 消</el-button>
        <el-button type="primary" @click="$emit('confirmCopyFile')"
          >确 定</el-button
        >
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "MoveFileDialog",
  props: {
    dialogCopyFile: Object,
  },
  data() {
    return {
      defaultProps: {
        children: "children",
        label: "label",
      },
    };
  },
  methods: {
    //  复制文件模态框：选择目录事件
    selectNodeClick(data) {
      let selectFilePath = data.attributes.filePath ? data.attributes.filePath: "/";
      this.$emit("setSelectFilePath", selectFilePath);
    },
  },
};
</script>

<style lang="stylus" scoped>
.copy-dialog-wrapper {
  >>> .el-dialog {
    .el-dialog__header {
      display: flex;
    }

    .el-dialog__body {
      padding: 10px 30px;

      .el-dialog-div {
        height: 300px;
        overflow: auto;
        setScrollbar(6px);

        .el-tree {
          .el-tree-node__content {
            height: 34px;
            font-size: 16px;

            .el-icon-caret-right {
              font-size: 18px;
            }
          }

          .el-tree-node.is-current>.el-tree-node__content {
            color: #409EFF;

            .el-tree-node__expand-icon {
              color: inherit;
            }
          }
        }
      }
    }
  }
}
</style>