<template>
  <div class="operation-menu-wrapper">
    <!-- 按钮组 -->
    <el-button-group class="operate-group">
      <!-- disabled 只在全部类型页面 才可新建文件夹 -->
      <el-button
        size="mini"
        type="primary"
        icon="el-icon-plus"
        v-show="fileType!=6"
        :disabled="fileType !== 0"
        @click="addFolderDialog.visible = true">新建文件夹</el-button>
      <el-button
        size="mini"
        type="primary"
        icon="el-icon-upload2"
        v-show="fileType!=6"
        :disabled="fileType !== 0"
        @click="handleUploadFileClick()">上传文件</el-button>
        <!-- disabled 当表格勾选项为空时，禁用删除按钮 -->
      <el-button
        size="mini"
        type="primary"
        icon="el-icon-delete"
        v-show="fileType!=6"
        :disabled="!operationFileList.length"
        @click="handleDeleteFileClick()">删除</el-button>
        <!-- disabled 当表格勾选项为空时，禁用移动按钮 | v-if 当左侧菜单选择全部时，才显示移动按钮 -->
      <el-button
        size="mini"
        type="primary"
        icon="el-icon-rank"
        v-show="fileType!=6"
        :disabled="!operationFileList.length"
        v-if="fileType === 0"
        @click="handleMoveFileClick()">移动</el-button>
        <!-- 复制操作-->
        <!-- <el-button
        size="mini"
        type="primary"
        icon="el-icon-rank"
        :disabled="!operationFileList.length"
        v-if="fileType === 0"
        @click="handleCopyFileClick()">复制到</el-button> -->
        <!-- disabled 当表格勾选项为空时，禁用下载按钮 -->
       
      <el-button
        size="mini"
        type="primary"
        icon="el-icon-download"
        v-show="fileType!=6"
        :disabled="!operationFileList.length"
        @click="handleDownloadFileClick()">下载</el-button>

        <el-button
        size="mini"
        type="primary"
        icon="el-icon-refresh-left"
        v-show="fileType==6"
        :disabled="!operationFileList.length"
        @click="handleRecoveryFileClick()">还原</el-button>

        <el-button
        size="mini"
        type="primary"
        icon="el-icon-delete"
        v-show="fileType==6"
        :disabled="!operationFileList.length"
        @click="handleDeleteRealFileClick()">删除</el-button>


      </el-button-group>

    <!-- 对话框 - 新建文件夹 -->
    <!-- @closed 对话框关闭动画结束时 重置表单并清空所有表单校验 -->
    <el-dialog
      title="新建文件夹"
      width="600px"
      :visible.sync="addFolderDialog.visible"
      @closed="$refs.addFolderForm.resetFields()"
    >
      <el-form
        :model="addFolderForm"
        :rules="addFolderRules"
        label-position="top"
        ref="addFolderForm"
      >
        <el-form-item label="文件夹名称" prop="name">
          <el-input v-model="addFolderForm.name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addFolderDialog.visible = false">取 消</el-button>
        <el-button
          type="primary"
          :loading="addFolderDialog.loading"
          @click="handleAddFolderSubmit('addFolderForm')"
          >提 交</el-button
        >
      </span>
    </el-dialog>
    <!-- 多选文件下载，页面隐藏 -->
    <a
      v-for="(item, index) in operationFileList"
      :key="index"
      :href="`/api/filetransfer/downloadfile?userFileId=${item.userFileId}`"
      :download="`${item.fileName}.${item.extendName}`"
      :ref="`downloadLink${index}`"
    ></a>
  </div>
</template>

<script >
import { createFile } from "@/request/file.js";
import { batchDeleteFile ,realDeleteFile, recoveryFile} from '@/request/file.js' //  引入批量删除文件接口

export default {
  name: "OperationMenu",
  props: {
    // 文件类型
    fileType: {
      type: Number,
      required: true,
    },
    // 文件路径
    filePath: {
      type: String,
      required: true,
    },
    // 表格行 已选项
    operationFileList: {
        type: Array,
        required: true
    }
  },
  data() {
    var validateName = (rule, value, callback) => {
      let str = new RegExp(
            "[`~!@#$^&*()=|{}':;',\\[\\].<>《》/?~！@#￥……&*（）——|{}【】‘；：”“'。，、？]"
          );
        if (str.test(value)) {
          callback(new Error('文件名不能包含*.?<|/\\等特殊字符！'));
        }
        if(value.length>30||value.length<1){
          callback(new Error('请输入1~30以内的字符！'));
        }
        callback(); 
      };
    return {
      // 新建文件夹对话框数据
      addFolderDialog: {
        visible: false, //  对话框显隐状态
        loading: false,
      },
      // 新建文件夹表单
      addFolderForm: {
        name: "",
      },
      // 新建文件夹表单校验规则
      addFolderRules: {
        name: [
          { required: true,message: "请输入文件夹名称",trigger: "blur, change",},
          {validator:validateName,trigger: "blur" },
        ],
      },
    };
  },
  methods: {
    // 上传文件按钮 - 点击事件
    handleUploadFileClick() {
      this.$emit('handleUploadFile', true)
    },
    // 新建文件夹对话框 - 提交按钮
    handleAddFolderSubmit(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.addFolderDialog.loading = true; //  对话框的确定按钮打开loading状态
          // 表单校验通过 - 调用新建文件夹接口
          createFile({
            fileName: this.addFolderForm.name,
            filePath: this.filePath, //  文件路径
            isDir: 1, //  是否为文件夹：1 表示文件夹 0 表示文件
          }).then(
            (res) => {
              this.addFolderDialog.loading = false; //  对话框的确定按钮关闭loading状态
              if (res.success) {
                this.$message.success("添加成功");
                this.addFolderDialog.visible = false; //  关闭对话框
                this.$emit("getTableData"); // 重新获取文件列表
              } else {
                this.$message.warning(res.message);
              }
            },
            (error) => {
              this.addFolderDialog.loading = false; //  对话框的确定按钮关闭loading状态
              console.log(error);
            }
          );
        } else {
          return false;
        }
      });
    },

    //彻底删除文件
    handleDeleteRealFileClick(){
       // 消息弹框提示用户
       this.$confirm('此操作将永久删除文件, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          // 确定按钮 点击事件 调用批量删除文件接口
          realDeleteFile({
            files: JSON.stringify(this.operationFileList)
          }).then((res) => {
            if (res.success) {
              this.$message({
                message: res.message,
                type: 'success'
              })
              this.$emit('getTableData') //  刷新文件列表
            } else {
              this.$message.error('失败' + res.message)
            }
          })
        })
        .catch(() => {
          //  取消
          this.$message({
            type: 'info',
            message: '已取消删除回收站文件'
          })
        })
    },

    // 批量删除文件按钮 - 点击事件
    handleDeleteFileClick() {
      // 消息弹框提示用户
      this.$confirm('此操作将删除文件至回收站, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          // 确定按钮 点击事件 调用批量删除文件接口
          batchDeleteFile({
            files: JSON.stringify(this.operationFileList)
          }).then((res) => {
            if (res.success) {
              this.$message({
                message: res.message,
                type: 'success'
              })
              this.$emit('getTableData') //  刷新文件列表
            } else {
              this.$message.error('失败' + res.message)
            }
          })
        })
        .catch(() => {
          //  取消
          this.$message({
            type: 'info',
            message: '已取消删除'
          })
        })
    },

    //还原文件
    handleRecoveryFileClick(){
       // 消息弹框提示用户
      this.$confirm('此操作将还原文件, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          
          //确定按钮 点击事件 调用还原文件接口
          recoveryFile({
            files: JSON.stringify(this.operationFileList)
          }).then((res) => {
            if (res.success) {
              this.$message({
                message: res.message,
                type: 'success'
              })
              this.$emit('getTableData') //  刷新文件列表
            } else {
              this.$message.error('失败' + res.message)
            }
          })
        })
        .catch(() => {
          //  取消
          this.$message({
            type: 'info',
            message: '已取消还原操作！'
          })
        })
    },

    // 批量移动文件按钮 - 点击事件
    handleMoveFileClick() {
        // true/false 批量移动/单文件操作 | this.operationFileList 当前行文件数据
        this.$emit('handleSelectFile', true, this.operationFileList)
        this.$emit('handleMoveFile', true) // true/false 打开/关闭移动文件对话框
    },
    //批量复制文件
    handleCopyFileClick(){
        this.$emit('handleSelectFile', true, this.operationFileList)
        this.$emit('handleCopyMoveFile', true) // true/false 打开/关闭复制文件对话框
    },
    // 下载文件按钮 - 点击事件
    handleDownloadFileClick() {
      for (let i = 0; i < this.operationFileList.length; i++) {
        this.$refs[`downloadLink${i}`][0].click() //  依次调用 a 标签的点击事件来下载文件
      }
    },
  },
};
</script>