import { get, post } from "./http";

// 登录接口
export const login = (p) => get("/user/login", p);

// 注册接口
export const addUser = (p) => post("/user/register", p);

// 发送注册邮件接口
// export const sendRegisterEmail = (p) => get(`/user/emailregistercode?email=${p}`);
export const sendRegisterEmail = (p) => get("/user/register/getcode", { email: p });

// 获取登录状态及用户信息
export const checkUserLoginInfo = (p) => get("/user/checkuserlogininfo", p);

// 忘记密码1，发送邮件获取修改密码的验证码
export const resetGetcode = (p) => get('/user/resetpwd/getcode', { email: p });
// 忘记密码2，修改密码
export const doreset = (p) => post("/user/resetpwd/doreset", p);
